<template>
  <div class="ebook-reader">
    <div id="read"></div>
    <!--  -->
    <div class="ebook-reader-mask"
         @click="onMaskClick"
         @touchmove="move"
         @touchend="moveEnd"
         @mousedown.left="mouseDown"
         @mousemove.left="mouseMove"
         @mouseup.left="mouseUp"
    ></div>
  </div>
</template>

<script>
import Epub from 'epubjs'
import { ebookMixin } from '@/utils/mixin/ebookMixin'
import { flatent } from '@/utils/book'
import { getFontFamily,
         saveFontFamily,
         getFontSize,
         saveFontSize,
         getTheme,
         saveTheme,
         getLocation
} from '@/utils/localStorage'
import { getLocalForage } from '@/utils/localForage'

global.ePub = Epub

export default {
  mounted () {
    // 
    let books = this.$route.params.fileName.split('|')
    // 
    let fileName = books[1]
    getLocalForage(fileName, (err, blob) => {
      if (!err && blob) {
        // 
        this.setFileName(books.join('/')).then(() => {
          this.initEpub(blob)
        })
      } else {
        // 
        this.setFileName(books.join('/')).then(() => {
         let url = `${process.env.VUE_APP_EPUB_URL}/${this.fileName}.epub`
          this.initEpub(url)
        })
      }
    })
  },
  mixins: [ebookMixin],
  methods: {
    // 
    initEpub (url) {
      this.book = new Epub(url)
      this.setCurrentBook(this.book) // this.bookVuex
      this.initRendition()
      this.parseBook()
      // 
      this.book.ready.then(() => {
        return this.book.locations.generate(750 * (window.innerWidth / 375) * getFontSize(this.fileName) / 16)
      }).then(locations => {
        // this.navigation.forEach(nav => {
        //   nav.pagelist = []
        // })
        // locations.forEach(item => {
        //   const loc = item.match(/\[(.*)\]!/)[1]
        //   this.navigation.forEach(nav => {
        //     // navhref
        //     if (nav.href) {
        //       const href = nav.href.match(/^(.*)\.html$/)
        //       if (href) {
        //         if (href[1] === loc) {
        //           nav.pagelist.push(item)
        //         }
        //       }
        //     }
        //   })
        //   let currentPage = 1
        //   this.navigation.forEach((nav, index) => {
        //     if (index === 0) {
        //       nav.page = 1
        //     } else {
        //       nav.page = currentPage
        //     }
        //     currentPage += nav.pagelist.length + 1
        //   })
        // })
        // Vuex
        // this.setPagelist(locations)
        this.setBookAvailable(true)
        this.refreshLocation() // progressnull
      })
    },
    // 
    prevPage () {
      if (this.rendition) {
        this.rendition.prev().then(() => {
          this.refreshLocation()
        })
        this.hideTitleAndMenu()
      }
    },
    // 
    nextPage () {
      if (this.rendition) {
        this.rendition.next().then(() => {
          this.refreshLocation()
        })
        this.hideTitleAndMenu()
      }
    },
    // menu 
    toggleTitleAndMenu () {
      if (this.menuVisible) {
        this.setSettingVisible(-1)
        this.setFontFamilyVisible(false)
      }
      // this.$store.dispatch('setMenuVisible', !this.menuVisible)
      this.setMenuVisible(!this.menuVisible)
    },
    // 
    initFontSize () {
      let fontSize = getFontSize(this.fileName)
      if (!fontSize) {
        saveFontSize(this.fileName, this.defaultFontSize)
      } else {
        this.setDefaultFontSize(fontSize)
        this.rendition.themes.fontSize(fontSize)
      }
    },
    initFontFamily () {
      let font = getFontFamily(this.fileName)

      if (!font) {
        saveFontFamily(this.fileName, this.defaultFontFamily)
      } else {
        this.rendition.themes.font(font)
        this.setDefaultFontFamily(font)
      }
    },
    initTheme () {
      // 
      let defaultTheme = getTheme(this.fileName)

      if (!defaultTheme) {
        defaultTheme = this.themeList[0].name
        saveTheme(this.fileName, defaultTheme)
      }
      this.setDefaultTheme(defaultTheme)
      // 
      this.themeList.forEach(theme => {
        this.rendition.themes.register(theme.name, theme.style)
      })
      // 
      this.rendition.themes.select(defaultTheme)
    },
    // rendition
    initRendition () {
      // rendition
      this.rendition = this.book.renderTo('read', {
        width: innerWidth,
        height: innerHeight,
        method: 'default' // 
      })
      // 
      const location = getLocation(this.fileName) // localstorage
      this.display(location, () => {
        this.initTheme()
        this.initFontFamily()
        this.initFontSize()
        this.initGlobalStyle()
        // this.refreshLocation()
      })
      // 
      this.rendition.hooks.content.register(contents => {
        // url
        Promise.all([
          // 
          contents.addStylesheet(`${process.env.VUE_APP_RES_URL}/fonts/family/cabin.css`),
          contents.addStylesheet(`${process.env.VUE_APP_RES_URL}/fonts/family/daysOne.css`),
          contents.addStylesheet(`${process.env.VUE_APP_RES_URL}/fonts/family/montserrat.css`),
          contents.addStylesheet(`${process.env.VUE_APP_RES_URL}/fonts/family/tangerine.css`)
        ]).then(() => {
          // 
        })
      })
    },
    // ()
    initGesture () {
      this.rendition.on('touchstart', event => {
        this.touchStartX = event.changedTouches[0].clientX
        this.startTime = event.timeStamp
      })
      this.rendition.on('touchend', event => {
        // 
        const offsetX = event.changedTouches[0].clientX - this.touchStartX
        const time = event.timeStamp - this.startTime
        // 
        if (time < 500 && offsetX > 40) {
          this.prevPage()
        } else if (time < 500 && offsetX < -40) {
          this.nextPage()
        } else {
          this.toggleTitleAndMenu()
        }
        this.defaultBehavior(event)
      })
    },
    // 
    parseBook () {
      /**
       * 
       *    1. 
       *    2. 
       *    3. 
      */
      // 1
      this.book.loaded.cover.then(cover => {
        this.book.archive.createUrl(cover).then(url => {
          this.setCover(url)
        })
      })
      // 2
      this.book.loaded.metadata.then(metadata => {
        this.setMetadata(metadata)
      })
      // 3
      this.book.loaded.navigation.then(navigation => {
        let newItem = flatent(navigation.toc)

        // ()
        function find (item, level = 0) {
          if (!item.parent) {
            return level
          } else {
            return find(newItem.filter(parentItem => parentItem.id === item.parent)[0], ++level)
          }
        }
        // level, 
        newItem.forEach((item, i) => {
          item.level = find(item)
        })
        this.setNavigation(newItem)
      })
    },
    // 
    onMaskClick (event) {
      if (this.mouseState && (this.mouseState === 2 || this.mouseState === 3)) {
        return
      }
      const offsetX = event.offsetX
      const width = window.innerWidth
      if (offsetX > 0 && offsetX < width * 0.3) {
        this.prevPage()
      } else if (offsetX > 0 && offsetX > width * 0.7) {
        this.nextPage()
      } else {
        this.toggleTitleAndMenu()
      }
    },
    move (event) {
      let offsetY = 0
      if (this.firstOffsetY) {
        offsetY = event.changedTouches[0].clientY - this.firstOffsetY
        this.setOffsetY(offsetY)
        // 
        this.defaultBehavior(event)
      } else {
        this.firstOffsetY = event.changedTouches[0].clientY
      }
    },
    // 
    moveEnd (event) {
      this.firstOffsetY = 0
      this.setOffsetY(0)
    },
    // pc -- 
    // 1 - 
    // 2 - 
    // 3 - 
    // 4 - 
    mouseDown (event) {
      this.mouseState = 1
      this.mosueStartTime = event.timeStamp
      this.defaultBehavior(event)
    },
    mouseMove (event) {
      if (this.mouseState === 1) {
        this.mouseState = 2
      } else if (this.mouseState === 2) {
        let offsetY = 0
        if (this.firstOffsetY) {
          offsetY = event.clientY - this.firstOffsetY
          this.setOffsetY(offsetY)
        } else {
          this.firstOffsetY = event.clientY
        }
      }
      this.defaultBehavior(event)
    },
    mouseUp (event) {
      if (this.mouseState === 2) {
        this.firstOffsetY = null
        this.setOffsetY(0)
        this.mouseState = 3
      } else {
        this.mouseState = 4
      }
      let time = event.timeStamp - this.mosueStartTime
      // ()
      if (time < 100) {
        this.mouseState = 4
      }
      this.defaultBehavior(event)
    },
    // 
    defaultBehavior (e) {
      // 
      e.preventDefault() // epubjs
      e.stopPropagation()
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '@/assets/styles/scss/global.scss';

  .ebook-reader {
    width: 100%;
    height: 100%;
    .ebook-reader-mask {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 150;
      width: 100%;
      height: 100%;
      background: transparent;
    }
  }
</style>
